@import '../variable.scss';
@import '../animation.scss';

.TitleCard {
    background-color: var(--soft-red);
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    color: var(--cream);
    padding: 1.1rem 1rem;
    border-radius: var(--border-radius);

    .logo {
        width: 20%;

        @media (min-width: $desktop-width) {
            width: 15%;
        }
    }
}